.hero {
  width: 100%;
  background-color: #eee;
  max-height: 1000px;
}
.hero .flexLeft {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: fit-content;
}
.hero-item{
  width: 50%;
  height: 100%;
  position: relative;
}
.hero h1 {
  color: #fff;
  font-size: 70px;
}
.hero p {
  color: #fff;
  opacity: 0.8;
}
form {
  background-color: #fff;
  border-radius: 5px;
  margin-top: 50px;
  padding: 0 20px;
}
.heroCalculator{
  margin-top: -80px;
}
form input {
  padding: 10px;
  width: 100%;
  border: 1px solid rgba(128, 128, 128, 0.2);
  margin-top: 5px;
  border-radius: 5px;
}
form span {
  font-size: 14px;
  color: grey;
}
input::placeholder {
  font-size: 17px;
  color: black;
}
form .box {
  padding: 15px;
  border-left: 1px solid rgba(128, 128, 128, 0.2);
}
form .box:nth-child(1) {
  border-left: none;
}
form h4 {
  font-weight: 500;
}
@media screen and (max-width: 800px) {
  .hero .container {
    padding-top: 30%;
  }
  form {
    display: flex;
    flex-wrap: wrap;
  }
  form .box {
    border-left: none;
  }
  form input {
    width: 100%;
  }
}
.heroTitle{
  font-size: 60px;
  color: #334;
  padding:0 30px 0 80px ;
  position: absolute;
  top: 5%;
  display: block;
  margin: auto;
}

.circleLeft{
  width: 420px;
  position: relative;
  height: auto; 
  animation: animLeft 5s ease-in-out infinite;
}

@keyframes animLeft {
  0%{
    top:0;
    left:0
  }
  50%{
    top:10%;
    left:0;
  }
  100%{
    left:0;
    top:0%;
  }
}
.circleRight{
  width: 350px;
  position: relative;
  height: auto; 
  animation: animRight 5s ease-in-out infinite;
}

@keyframes animRight {
  0%{
    bottom:5%;
    right:-5%;
  }
  50%{
    bottom:15%;
    right:-5%;
  }
  100%{
    bottom:5%;
    right:-5%;
  }
}
